.l-block--docs-list {
  ul {
    display: flex;
    flex-direction: column;
  }
  li {
    margin-bottom: 25px;
  }
  a {
    display: flex;
    align-items: center;
    transition: .2s;
    &:hover {
      color: $color_blue;
      text-decoration: underline;
    }
    @include xm {
      align-items: flex-start;
    }
  }
  i {
    display: flex;
    width: 32px;
    height: 32px;
    margin-right: 15px;
    flex-shrink: 0;
    @include xm {
      margin-right: 8px;
    }
  }
}

@media screen and (max-width: 425px) {
  .l-invest-svod--grid {
    &::after {
      display: none;
    }
  }
  .l-block--docs-list {
    li {
      margin-bottom: 20px;
    }
    i {
      // width: 13px;
      // height: 17px;
    }
  }
}