.l-agro__cards {
  ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    @include m-880 {
      grid-template-columns: 1fr;
    }
  }
}

.l-card__location {
  padding: 30px;
  transition: all .4s ease;
  width: 100%;
  border: 1px solid #1D1E33;
  display: flex ;
  flex-direction: column;
  gap: 20px;
  height: 280px;
  position: relative;
  @include m-small {
    height: 190px;
    padding: 20px;
    gap: 10px;
  }
  .l-title {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .arrow-small {
    position: absolute;
    right: 30px;
    top: 30px;
    max-height: 0;
    width: 32px;
    @include m-small {
      right: 20px;
      top: 20px;
      width: 24px;
      height: 24px;
    }
  }
  &:hover {
    color: #f9f9f9;
    background-color: #1D1E33;
    .arrow-small {
      max-height: 32px;
    }
    .location {
      background: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.0009 13.4299C13.724 13.4299 15.1209 12.0331 15.1209 10.3099C15.1209 8.58681 13.724 7.18994 12.0009 7.18994C10.2777 7.18994 8.88086 8.58681 8.88086 10.3099C8.88086 12.0331 10.2777 13.4299 12.0009 13.4299Z' stroke='%23F9F9F9' stroke-width='1.5'/%3E%3Cpath d='M3.61971 8.49C5.58971 -0.169998 18.4197 -0.159997 20.3797 8.5C21.5297 13.58 18.3697 17.88 15.5997 20.54C13.5897 22.48 10.4097 22.48 8.38971 20.54C5.62971 17.88 2.46971 13.57 3.61971 8.49Z' stroke='%23F9F9F9' stroke-width='1.5'/%3E%3C/svg%3E%0A")
      no-repeat center/cover;
    }
  }

  p {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    @include m-small {
      font-size: 12px;
      line-height: 120%;
    }
  }
}

.l-invest__projects {
 ul {
  grid-template-columns: 1fr 1fr 1fr;
  @include l-1150 {
    grid-template-columns: 1fr 1fr;
  }
  @include m-640 {
    grid-template-columns: 1fr;
  }
 }
