.l-news-page__section {
  padding-bottom: 100px;

  .l-news__grid {
    grid-template-columns: 1fr 1fr 1fr;
    .l-item {
      article {
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: space-between;
      }
    }
    @include xm {
      grid-template-columns: 1fr 1fr;
    }
    @include m-small {
      grid-template-columns: 1fr;
      grid-template-rows: auto;
      .l-item {
        height: 185px;
      }
    }
    .l-item {
      &:nth-child(4) {
        grid-column-start: unset;
      }
      max-width: 100%;
      &:first-child {
        grid-column-start: 2 span;
        grid-row-start: 2 span;
        height: 100%;
        @include xm {
          grid-column-start: unset;
          grid-row-start: unset;
        }
        @include m-small {
          height: 185px;
        }
      } 
    }
  }
}

.l-news__pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-top: 40px;
  .arrow-pag {
    padding: 0;
  }
  svg.arrow-red {
    display: block;
    width: 40px;
    height: 40px;
  }
  a {
    padding: 5px 12px ;
    margin-right: 20px;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.06em;
    color: #858585;
    transition: all .4s;
    @include m-small {
      margin-right: 12px;
    }
    &:hover {
      color: #1D1E33;
    }
    &.active {
      color: #1D1E33;
      text-decoration-line: underline;
    }
    &:first-child {
      margin-right: 30px;
      @include m-small {
        margin-right: 18px;
      }
    }
    &:last-child {
      padding-right: 10px;
      margin-right: unset;
      transform: rotate(180deg);
      @include m-small {
        padding-right: 8px;
      }
    }
  }

}
.l-news__card {
  width: 100%;
  position: relative;
  padding: 30px;
  z-index: 1;
  transition: all 0.4s;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include m-small {
    padding: 15px 15px 20px 15px;
  }
  .descr {
    margin-top: 15px;
  }
  &:hover {
    img {
      opacity: 1;
    }
  }
  img {
    transition: all 0.4s;
    position: absolute;
    opacity: 0.7;
    left: 0;
    top: 0;
    height: 100%;
    z-index: -1;
    width: 100%;
  }
}

.l-media {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap:20px;
  @include xm {
    grid-template-columns: 1fr;

  }
  article {
    height: 100%;
    display: flex;
    flex-direction: column;
    .flex {
      margin-top: auto;
    }
  }
  a {
    height: 400px;
    @include m-small {
      height: 185px;
    }
  }
}

.l-media__list {  
  li {
    margin-bottom: 25px;
    padding-bottom: 10px;
    border-bottom: 1px solid #858585;
    &:last-child {
      border-bottom: unset;
      a {
        margin: 0 auto;
        margin-top: 15px;
      }

    }
  }
}

.l-map__section {
  @include xxm {
    grid-template-columns: 1fr;
    gap: 0;
    hr {
      display: none;
    }
  }
  hr {
    margin-top: 60px;
    max-height: 55%;
  }
  .tablet-hide {
    @include xxm {
      display: none;
    }
  }
  .tablet-show {
    @include xxm {
      display: block;
      .l-btn {
        margin: 40px auto 0 auto;
      }
    }
  }
  .l-map__insert {
    width: 100%;
    max-width: 750px;
    height: 450px;
    margin-top: 20px;
    @include xxm {
      max-width: 100%;
    }
    @include xm {
      height: 300px;
    }
    @include xxs {
      height: 190px;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
}

.l-map__social {
  margin-top: 60px;
  .l-social__vertical {
    svg {
      stroke: #1D1E33;
      width: 32px;
      height: 32px;
    }
  }
  .row {
    flex-direction: row;
    justify-content: space-between;
    max-width: 408px;
    @include xl-1300 {
      flex-direction: column;
    }
  }
}

.l-news__detail-page {

  .l-page_banner--wrap {
    @include m-small {
      height: 325px;
    }
  }
  .l-page--content {
    border-bottom: 1px solid #858585;
    padding-bottom: 40px;
  }
  .bottom {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    @include m-small {
      flex-direction: column;
      gap: 20px;
    }
  }
}