.js-swiper-pag {
  max-width: 460px;
  position: relative;
  bottom: -40px;
  @include m-small {
    max-width: 304px;
  }
}

.js-swiper__detail-page {
  .js-swiper-pag {
    @include m-small {
      max-width: 304px;
    }
  }
}

.success-button-next {
  cursor: pointer;
  position: absolute;
  right: 0;
  width: 40px;
  height: 40px;

  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24.3346 30L26.668 27.6667L19.0013 20L26.668 12.3333L24.3346 10L14.3346 20L24.3346 30Z' fill='%231D1E33'/%3E%3C/svg%3E%0A");
  transform: rotate(180deg);
  @include m-small {
    width: 32px;
    height: 32px;
    background-position: center;
  }
}
.success-button-prev {
  cursor: pointer;
  position: absolute;
  left: 0;
  width: 40px;
  height: 40px;
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24.3346 30L26.668 27.6667L19.0013 20L26.668 12.3333L24.3346 10L14.3346 20L24.3346 30Z' fill='%231D1E33'/%3E%3C/svg%3E%0A");
  @include m-small {
    width: 32px;
    height: 32px;
    background-position: center;
  }
}

.success-pagination {
  background-color: #858585;
  width: 100%;
  height: 10px;
  bottom: 0 !important;
  top: unset !important;
  right: 0;
  width: 300px !important;
  margin: 0 auto !important;
  @include m-small {
    width: 200px !important;
  }
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background: #ff1a1a !important;
}

.l-project__descr-body {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 10px;
  position: relative;
  @include m-950 {
    flex-direction: column;
    gap: 50px;
  }
  .l-list {
    &:first-child {
      max-width: 900px;
      padding-right: 50px;
      border-right: 1px solid #858585;
      @include m-950 {
        border-right: unset;
        padding-right: unset;
      }
    }
    &:last-child {
      padding-left: 50px;
      @include m-950 {
        padding-left: unset;
      }
    }
    ul {
      margin-left: 20px;
      margin-top: 28px;
      display: flex;
      flex-direction: column;
      li {
        list-style: disc;
        margin-bottom: 28px;
        &:last-child {
          margin-bottom: unset;
        }
      }
    }
  }
}

.l-project__descr-section {
  .l-btn {
    margin: 0 auto;
    margin-top: 40px;
  }
}

.l-modal__component {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;

  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  opacity: 0;
  transition: all 0.4s ease;
  padding: 20px 30px;
  @include xxs {
    padding: 0;
  }
  &.active {
    opacity: 1;
    pointer-events: auto;
  }

  .close {
    position: absolute;
    right: 30px;
    top: 30px;
    width: 32px;
    height: 32px;
    z-index: 1000;
    @include m-small {
      top: 15px;
      right: 15px;
    }
    svg {
      fill: #1d1e33;
      width: 100%;
      height: 100%;
    }
  }
  .l-content {
    position: relative;
    background-color: white;
    max-width: 860px;
    width: 100%;

    z-index: 1010;
    padding: 80px 90px;

    display: flex;
    align-items: center;
    flex-direction: column;
    transition: all 0.4s ease;

    @include m-small {
      padding: 50px 25px 40px 25px;
      text-align: center;
    }
  }
  .l-btn {
    width: 100%;
    padding: 20px 38px;
    background-color: #1d1e33;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #f9f9f9;
    cursor: pointer;
    transition: all 0.4s;
    @include m-small {
      font-size: 12px;
      line-height: 120%;
      letter-spacing: 0.04em;
    }
    &:hover {
      color: #ff1a1a;
    }
  }
  .btns {
    margin-top: 50px;
    flex-direction: column;
    width: 100%;
  }
  .bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: black;
    opacity: 0.7;
    z-index: 1001;
  }
}
