.color-select--white {
  .select-selected {
    background-color: $color_white;
    color: $color_dark;

    border: 1px solid $color_dark;
  }
  .select-items {
    background-color: $color_white;
    color: $color_dark;

    border: 1px solid $color_dark;
    border-top: none;
    .same-as-selected {
      color: $color_red;
    }
    div {
      transition: .2s;
      &:hover {
        color: $color_red;
      }
    }
  }
  .select-items2 {
    background-color: $color_white;
    color: $color_dark;

    border: 1px solid $color_dark;
    border-top: none;
    .same-as-selected {
      color: $color_red;
    }
    div {
      transition: .2s;
      &:hover {
        color: $color_red;
      }
    }
  }
  .custom-select__arrow svg {
    stroke: $color_dark;
  }
}

.color-select--black {
  .select-selected {
    background-color: $color_dark;
    color: $color_white;
  }
  .select-items {
    background-color: $color_dark;
    color: $color_white;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
    .same-as-selected {
      color: $color_red;
    }
    div {
      transition: .2s;
      &:hover {
        color: $color_red;
      }
    }
  }
  .select-items2 {
    background-color: $color_dark;
    color: $color_white;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
    .same-as-selected {
      color: $color_red;
    }
    div {
      transition: .2s;
      &:hover {
        color: $color_red;
      }
    }
  }
  .custom-select__arrow svg {
    stroke: $color_white;
  }
}

.custom-select {
  position: relative;
  height: auto;
  width: 100%;
}
.custom-select select {
  display: none;
}
.select-selected {
  cursor: pointer;
  height: auto;
  position: relative;
  z-index: 3;
  width: 100%;
  //color black
  padding: 18px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  letter-spacing: .06em;
  //color black

}

.custom-select__arrow {
  width: 24px;
  height: 24px;
}
.--svg__accordion-arrow {
  width: 100%;
  height: 100%;
  transition: .3s;
}

svg {
  width: 100%;
  height: 100%;
  transition: .3s;
}

.select-arrow-active {
  // .--svg__accordion-arrow {
  //   transform: rotate(180deg);
  // }
  svg {
    transform: rotate(180deg);
  }
}

.select-items {
  position: static;
  //color black
  height: 460px;
  top: 60px;
  left: 0;
  right: 0;
  padding: 30px 30px;
  z-index: 2;
  overflow-y: scroll;
  div {
    margin-bottom: 20px;
    cursor: pointer;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background-color: $color_white;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $color_red;
  }
  @include xm {
    padding: 15px 20px;
  }
}
.select-hide {
  display: none;
}

