.l-content-obrashenie {
  width: 1300px;
  @include l {
    width: 100%;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    @include l {
      display: grid;
      grid-template-columns: 1fr;
      gap: 40px;
    }
    li {
      display: flex;
      align-items: flex-start;
      gap: 10px;
      position: relative;
    }
    .parent-flex {
      &:nth-child(1) {
        max-width: 260px;
        margin-right: 235px;
        @include l {
          max-width: 100%;
          margin-right: 0;
        }
        &::after {
          content: "";
          position: absolute;
          right: -220px;
          top: 50%;
          width: 205px;
          height: 1px;
          background-color: #858585;
          @include l {
            top: 147%;
            right: unset;
            left: 7px;
            width: 28px;
            transform: rotate(90deg);
          }
          @include xxs {
            left: 2px ;
            top: 156%;
          }
        }

      }
      &:nth-child(2) {
        max-width: 314px;
        margin-right: 147px;
        @include l {
          margin-right: 0;
          max-width: 100%;
        }
        &::after {
          content: "";
          position: absolute;
          right: -130px;
          top: 20px;
          width: 117px;
          height: 1px;
          background-color: #858585;
          @include l {
            right: unset;
            top: 100px;
            width: 94px;
            left: -27px;
            transform: rotate(90deg);
          }
          @include xxs {
            left: -14px;
            top: 77px;
            width: 60px;
          }
        }

      }
      &:nth-child(3) {
        max-width: 320px;

        &::after {
          content: "";
          position: absolute;
          top: 55%;
          left: -41px;
          width: 123px;
          height: 1px;
          background-color: #858585;
          bottom: 0;
          transform: rotate(90deg);
          @include l {
            top: 66%;
            left: -108px;
            width: 257px;
          }
          @include xxs {
            top: 68%;
            left: -104px;
            width: 240px;
          }
        }
        .child-list {
          ul {
            margin-left: 15px;
            li {
              list-style: disc;
              &::before {
                content: "";
                position: absolute;
                left: -10px;
                top: 38%;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background-color: #858585;
              }
            }
          }
        }

      }
      &:nth-child(4) {
        max-width: 625px;
        order: 5;
        margin-top: 15px;
        margin-left: 47px;
        @include l {
          margin-left: 0;
        }
        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: -15%;
          width: 40px;
          transform: rotate(90deg);
          height: 1px;
          background-color: #858585;
          @include xxs {
            left: -18px;
            top: -22%;
            width: 70px;
          }
        }
        &::before {
          content: "";
          position: absolute;
          top: 59%;
          width: 139px;
          -webkit-transform: rotate(90deg);
          -ms-transform: rotate(90deg);
          transform: rotate(90deg);
          height: 1px;
          background-color: #858585;
          left: -49px;
          @include xm {
            display: none;
          }
        }
        .child-list-big {
          margin: 40px 0 11px 0;
          @include xm { 
            margin: 15px 0 10px 0;
          }
          &::after {
            content: "";
            position: absolute;
            left: -31px;
            top: 22%;
            width: 22px;
            height: 1px;
            background-color: #858585;
            @include xm {
              display: none;
            }
          }
          li {
            display: flex;
            flex-direction: column;
            gap: 10px;
          }
        }
        .l-text-list {
          &:last-child {
            position: relative;
            &::after {
              content: "";
              position: absolute;
              left: -31px;
              width: 22px;
              height: 1px;
              background-color: #858585;
              bottom: 12px;
              @include xm {
                display: none;
              }
            }
          }
        }
      }
      &:nth-child(5) {
        margin-top: 15px;
        max-width: 450px;

        &::before {
          content: "";
          position: absolute;
          left: 18px;
          top: -66%;
          width: 213%;
          height: 1px;
          background-color: #858585;
          @include l {
            display: none;
          }
        }

        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: -40%;
          width: 40px;
          transform: rotate(90deg);
          height: 1px;
          background-color: #858585;
          @include l {
            display: none;
          }
        }

      }
    }
  }
  .l-text-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
  }
  .l-number {
    min-width: 40px;
    min-height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #1d1e33;

    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #ff1a1a;
    @include xxs {
      min-width: 32px;
      min-height: 32px;
      font-size: 16px;
    }
  }
}
.l-form__content {
  display: grid;
  grid-template-columns: minmax(500px, 750px) 6px 1fr;
  gap: 50px;
  hr {
    width: 1px;
    height: 100%;
    max-height: 85%;
    background-color: #858585;
    @include xm {
      display: none;
    }
  }

  @include xl {
    grid-template-columns: minmax(500px,650px) 6px 1fr;
  }
  @include l {
    margin-top: 40px;
    grid-template-columns: minmax(500px,500px) 6px 1fr;
  }
  @include xxm {
    gap: 25px;
  }
  @include xm {
    display: flex;
    flex-direction: column;
    gap: 60px;
    margin-top: 0;
  }
}

.l-form__form {
  margin-top: 28px;
  .checkbox {
    width: 24px;
    height: 24px;
    border: 1px solid #1d1e33;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    input {
      width: 0;
      height: 0;
      &:checked ~ .checkmark {
        display: block;
      }
    }
    .checkmark {
      position: absolute;
      width: 12px;
      height: 12px;
      display: none;
      background-color: #1d1e33;
    }
  }
  .l-input {
    display: flex;
    gap: 10px;
    flex-direction: column;
    width: 100%;
    margin-top: 25px;

    &:first-child {
      margin-top: unset;
    }
    input {
      border: 1px solid #1d1e33;
      padding: 18px 30px;
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.06em;
      color: #1d1e33;
      transition: all 0.4s;

      &:focus-within {
        outline: #858585;
        box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
      }
      &::placeholder {
        color: #858585;
        
      }
      @include xm {
        padding: 15px 20px;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        letter-spacing: 0.04em;
      }
    }
    textarea {
      border: 1px solid #1d1e33;
      padding: 18px 30px;
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.06em;
      color: #1d1e33;
      resize: vertical;
      &:focus-within {
        outline: #858585;
        box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
      }
      &::placeholder {
        color: #858585;
        
      }
      @include xm {
        padding: 15px 20px;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        letter-spacing: 0.04em;
      }
    }
    @include xm {
      margin-top: 20px;
    }
    @include xm {
      margin-top: 20px;
    }
  }
}

.select-selected {
  @include xm {
    padding: 15px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    letter-spacing: 0.04em;
  }
}

.select-selected {
  @include xm {
    padding: 15px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    letter-spacing: 0.04em;
  }
}

.file-label {
  border: 1px solid #1d1e33;
  padding: 18px 30px;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  letter-spacing: 0.06em;
  color: #1d1e33;
  transition: all 0.4s;
  position: relative;
  cursor: pointer;
  .--svg__file {
    position: absolute;
    right: 30px;
    top: 27%;
    width: 32px;
    height: 32px;
    background-size: 100%;
    background-repeat: no-repeat;
  }
}

.l-form__inputs {
  max-width: 750px;
  width: 100%;
}

.l-social__vertical {
  display: flex;
  flex-direction: column;
  margin-top: 34px;
  li {
    margin-bottom: 25px;
    i {
      width: 32px;
      height: 32px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: 100%;
    }
    &:last-child {
      margin-bottom: unset;
    }
  }
  a {
    transition: .2s;
    &:hover {
      color: $color_red;
      svg {
        fill: $color_red;
      }
    }
  }
  @include xm {
    margin-top: 20px;
  }
}

.l-soglasie {
  margin-top: 25px;
}
.file-name {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 300px;
  display: block;
}

.pb-100 {
  padding-bottom: 100px;
}

.hidden-input {
  display: none;
}
.enable-input {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.l-feedback__list-pick {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 20px;
  max-width: 1070px;
  white-space: nowrap;
  @include xl {
    grid-template-columns: 1fr;
    white-space: normal;
  }
  li {
    border: 1px solid #1d1e33;
  }
  a {
    padding: 22px 30px;
    display: block;

    transition: all 0.3s;
    @include m-small {
      padding: 12px 20px;
    }
    &:hover {
      background-color: #1d1e33;
      color: white;
    }
  }
}


.l-feedback__person--container {
  display: flex;
  gap: 32px;
  @include xm {
    flex-direction: column;
    gap: 30px;
  }
}

.l-feedback__person--left {
  max-width: 310px;
  width: 100%;
  @include xm {
    max-width: 325px;
    margin: 0 auto;
  }
  @include xxs {
    max-width: 100%;
  } 
}

.l-feedback__person--photo {
  height: 390px;
  img {
    @include xxs {
      width: 100%;
      height: 100%;
    } 
  }
}

.l-feedback__person--right {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.l-feedback__person--title {

}

.l-feedback__person--quote {
  padding: 27px 0;
  border-bottom: 1px solid $color_gray;
  @include xxs {
    padding: 20px 0;
  }
}

.l-feedback__person--footer {
  margin-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.l-feedback__person--name {

}

.l-feedback__person--desc {
  margin-top: 10px;
}
