.l-banner__bg {
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  height: 100%;
  min-height: 800px;
  z-index: -2;
  @include xm {
    min-height: 580px;
  }
  .bg-ottenok {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
  }
}
.big-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  z-index: -2;
  object-fit: cover;
}

.l-about__bottom-text {
  padding-bottom: 50px;
  margin-top: auto;
  padding-top: 50px;
  @include m-small {
    padding-bottom: 20px;
  }
}
.hr-20 {
  border-bottom: 1px solid #F9F9F9;
  margin: 20px 0;
}

.l-big-face {

  .l-page--header {
    min-height: 800px;
    position: relative;
    .l-page--sections-list {
      @include xm {
        display: none;
      }
    }
    @include xm {
      min-height: 580px;

    }
  }
  .l-breadcrumbs {
    li {
      span,
      a {
        color: #F9F9F9 ;
      }
    }
  }
  .l-page--sections-list {
    i.separator {
      background: #F9F9F9;
    }
    a {
      color: #F9F9F9 ;
    }
  }
}

.l-agent {
  padding-bottom: unset;

  .l-about__bottom-text,
  .l-page--sections-list {
    max-width: 50%;
    @include xm {
      max-width: 100%;
    }
  }
}

.l-about-section-1 {
  padding-bottom: 0;
  p {
    margin-top: 28px;
  }
  .l-numbers__content {
    margin-top: 40px;
  }
}

.l-about__gallery  {
  li {
    margin-bottom: 80px;
    @include m-950 {
      margin-bottom: 60px;
    }

    .flex {
      @include m-950 {
        flex-direction: column;
      }
    }
    &:nth-child(even) {
      .item-text {
        order: 2;
        @include m-950 {
          order: unset;
        }
      }
    }
  }
}

.l-about-section-2 {
  padding-bottom: unset;
  
  .item-text {
    max-width: 407px;
    @include m-950 {
      max-width: 100%;
    }
  }
}

.l-about__video {
  text-align: center;
  @include m-small {
    text-align: start;
  }
  .l-block--page-banner {
    margin-top: 50px;
    @include m-small {
      margin-top: 20px;
    }
    video {
      object-fit: cover;
      width: 100%;
    }
  }
}