.l-invest-svod-pravil--undertitle {
  a {
    color: $color_blue;
    transition: .2s;
    &:hover {
      color: $color_red;

    }
  }
}
.l-invest-svod--grid {
  position: relative;
  margin-top: 80px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  row-gap: 40px;
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    background: url('/img/suip_bg_all.png');
    background-repeat: no-repeat;
    max-width: 600px;
    width: 100%;
    max-height: 280px;
    height: 100%;
  }
}
.l-invest-svod--item {
  max-width: 640px;
  width: 100%;
  max-height: 455px;
  height: 100%;
}
.l-invest-svod--title {
  display: flex;
  align-items: center;
}
.l-invest-svod--counter {
  border: 1px solid $color_dark;
  border-radius: 100%;
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  color: $color_red;

  flex-shrink: 0;
  @include xm {
    width: 40px;
    height: 40px;
  }
}

.l-invest-svod--img {
  margin-top: 20px;
  max-width: 640px;
  width: 100%;
  max-height: 380px;
  height: auto;
  img {
    width: 100%;
    height: 100%;
  }
}
@media screen and (max-width: 560px) {
  .l-invest-svod--grid {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 425px) {
  .l-invest-svod-pravil--undertitle {
    display: flex;
    flex-direction: column;
    span {
      margin-bottom: 10px;
    }
  }
  .l-invest-svod--grid {
    grid-template-columns: 1fr;
  }

  .l-invest-svod--counter {
    width: 32px;
    height: 32px;
    margin-right: 10px;
  }
  .l-invest-svod--img {
    margin-top: 15px;
    height: auto;
  }
}