.l-block--person-card {
  margin-top: 30px;
}

.l-person__card--container {
  display: flex;
  gap: 20px;
  @include m-small {
    flex-direction: column;
  }
}
.l-person__card--left {

}

.l-person__card--img {
  max-width: 345px;
  width: 100%;
  max-height: 345px;
  height: auto;
  @include m-small {
    margin: 0 auto;
    max-width: 325px;
    max-height: 325px;
  }
  img {
    width: 100%;
    height: 100%;
  }
}

.l-person__card--right {
  max-width: 345px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.l-person__card--name {
  margin-bottom: 10px;
}

.l-person__card--desc {
}

.l-person__card--hr {
  margin: 15px 0;
  hr {
    width: 100%;
    height: 1px;
    background: $color_gray;
  }
}

.l-person__card--contact {
  margin-bottom: 10px;
  span {
    margin-right: 8px;
  }
  &:last-child {
    margin-bottom: 0;
  }
  a {
    transition: .2s;
    &:hover {
      color: $color_red;
    }
  }
}
