.l-footer {
  background-color: $color_dark;
  height: 315px;
  @include l {
    height: 100%;
    min-height: 315px;
  }
  hr {
    border-bottom: 1px solid #FFFFFF;
  }
}

.l-footer__logo {
  height: 20px;
  width: 250px;
  display: block;
  @include m-small {
    max-width: 188px;

  }
}
.l-footer__content {
  padding-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  padding-bottom: 20px;
  @include xm {
    flex-direction: column;
    align-items: flex-start;
  }
  @include m-small {
    padding-top: 25px;
  }
}

.l-footer__left-list {
  max-width: 95%;
  margin-top: 40px;
  @include l {
    max-width: 100%;

  }
  @include m-small {
    margin-top: 30px;
  }
  ul {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    @include l {
      flex-direction: column;

    }
    li {
      @include l {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: unset;
        }
      }
      &:first-child {
        margin-right: 80px;
      }
    }
  }
}

.l-list-item {
  display: flex;
  align-items: flex-start;
  gap: 22px;
  @include xxs {
    gap: 10px;
  }
  svg {
    width: 24px;
    height: 24px;
    stroke: #FFFFFF;
  }
}

.l-social__list {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 40px;
  margin-top: 20px;
  @include xm {
    justify-content: flex-start;
  }
  a {
    transition: all .4;
    &:hover {
      svg {
        fill: #FF1A1A;
      }
    }
    svg{
      width: 24px;
      height: 24px;
      fill: white;
    }
  }
}

.logo-platforma {
  display: flex;
  align-items: center;
  gap: 5px;
  @include m-small {
    justify-content: flex-end;
  }
  a {
    background: url("data:image/svg+xml,%3Csvg width='17' height='16' viewBox='0 0 17 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.904541 0H16.1045L8.63787 7.32632L0.904541 14.4V0Z' fill='%23F9F9F9'/%3E%3Cpath d='M16.9045 16H1.70454L9.17121 8.67369L16.9045 1.6V16Z' fill='%23F9F9F9'/%3E%3C/svg%3E%0A")

    no-repeat center/cover;
    width: 16px;
    height: 16px;
    display: block;
  }

}

.l-footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  @include xm {
    display: grid;
    grid-template-columns:  1fr 125px;
    gap: 20px;
    a {
      grid-row: 1;
      grid-column-start: 2 span;
    }
  }

}