
.l-municip--accrodion {
  margin-bottom: 50px;
  @include xm {
    margin-bottom: 30px;
  }
}

.l-municip--container {
  display: flex;
  gap: 25px;
  @include xm {
    flex-direction: column-reverse;
    gap: 30px;
  }
}

.l-municip--content {

}

.l-municip--left {
  max-width: 75%;
  width: 100%;
  .l-municip--footer {
    display: none;
  }
  @include xm {
    max-width: 100%;
    .l-municip--content {
      padding-bottom: 34px;
      border-bottom: 1px solid $color_gray;
    }
    .l-municip--footer {
      display: flex;
      margin-top: 30px;
      gap: 30px;
    }
  }
}

.l-block--title {
  margin-bottom: 28px;
}

.l-municip--right {
  border-left: 1px solid $color_gray;
  padding-left: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 300px;
  @include xm {
    padding-left: 0;
    border-left: none;
    .l-municip--footer {
      display: none;
    }
  }
}

.l-municip--img {
  max-width: 256px;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 100%;
    height: 100%;
  }
}

.l-municip--footer {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.l-municip--links {
  display: flex;
  flex-direction: column;
  span {
    margin-bottom: 8px;
  }
  a {
    transition: .2s;
    &:hover {
      color: $color_red;
    }
  }
}

.l-municip--socials {
  ul {
    display: flex;
    gap: 30px;
    margin-top: 20px;
  }
  i {
    display: flex;
    width: 24px;
    height: 24px;
  }
  a:hover {
    svg {
      fill: $color_red;
    }
  }
}
