:root {
  --color-blue: linear-gradient(83.49deg, #175DF8 10.09%, #004EFB 98.26%);
  --color-blue2: #024FFA;
  --color-blue-light: #F0F5FB;
  --color-blue-light2: #CADCF2;
  --color-blue-light3: #DDE9F5;
  --color-blue-dark: #2E3449;
  --color-green: linear-gradient(62.92deg, #37C239 0%, #31DC33 0.01%, #2DE72F 100%);
  --color-grey-light: #B8B4B4;
  --color-white: #ffffff;
  --color-black: #000000;
  --color-grey: #1D1E33;
  --day-border: 1px solid var(--color-grey);
  --day-border-highlight: 1px solid #b8c2cc;
  --day-width: 90px;
  --day-height: 90px;
  --weekday-bg: #f8fafc;
  --weekday-border: 1px solid #eaeaea;
}

.calendar {
  margin-top: 80px;
}
@media (max-width: 1080px) {
  .calendar {
    margin-top: 60px;
  }
}

.custom-calendar.vc-container {
  border-radius: 0;
  border: none;
  width: 1080px;
}
.custom-calendar.vc-container .vc-header {
  background-color: #f1f5f8;
  padding: 10px 0;
  display: none;
}
.custom-calendar.vc-container .vc-weeks {
  padding: 0;
  overflow: hidden;
  border: 1px solid var(--color-grey);
}
.custom-calendar.vc-container .vc-weekday {
  background-color: $color_dark;
  color: var(--color-white);
  border-right: 1px solid $color_white;
  font-family: 'Gilroy';
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;

  letter-spacing: 0.06em;
  padding: 23px 20px;
  text-align: left;

  text-transform: capitalize;
}

@media (max-width: 1080px) {
  .custom-calendar.vc-container .vc-weekday {
    padding: 15px 20px;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    letter-spacing: 0.04em;
  }
}

.custom-calendar.vc-container .vc-weekday:first-child {
  border-left: none;
}

.custom-calendar.vc-container .vc-weekday:nth-child(7) {
  border-right: none;
}

.custom-calendar.vc-container .vc-day {
  padding: 15px 20px;
  text-align: left;
  height: auto;
  min-width: var(--day-width);
  background-color: white;

  font-family: 'Gilroy';
}

.custom-calendar.vc-container .vc-day .flex-col {
  flex-direction: column;
}

.custom-calendar.vc-container .vc-day .weekday-1, .custom-calendar.vc-container .vc-day .weekday-7 {
  background-color: #eff8ff;
}
.custom-calendar.vc-container .vc-day:not(.on-bottom) {
  border-bottom: var(--day-border);
}
.custom-calendar.vc-container .vc-day:not(.on-bottom) .weekday-1 {
  border-bottom: var(--day-border-highlight);
}
.custom-calendar.vc-container .vc-day:not(.on-right) {
  border-right: var(--day-border);
}
.custom-calendar.vc-container .vc-day:nth-child(7n), .custom-calendar.vc-container .vc-day:nth-child(7n-1) {
  background-color: #E96060;
}
.custom-calendar.vc-container .vc-day-dots {
  margin-bottom: 5px;
}

.day-label {
  display: block;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}

.calendar__event {
  padding-top: 15px;
  margin-top: 10px;
}
.calendar__event:first-child {
  border-top: 1px solid $color_dark;
}

.is-not-in-month {
  
}
.is-not-in-month div {
  opacity: 1 !important;
}
.is-not-in-month .calendar__event {
  opacity: 0 !important;
  display: none;
}
.is-not-in-month .day-label {
  opacity: 1 !important;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: var(--color-blue-dark) !important;
}

.calendar__event-time {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 5px;
}

.calendar__event-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $color_dark;
  &:hover {
    color: $color_blue;
  }
}

.calendar__swiper-paggination-wrap {
  position: relative;
  margin-top: 26px;
  height: 3px;
}
.calendar__swiper-paggination-wrap .calendar__swiper-paggination {
  height: 1px;
  margin: 1px 0;
}
.calendar__swiper-paggination-wrap .calendar__swiper-paggination .swiper-pagination-progressbar-fill {
  top: -1px;
  height: 3px;
  border-radius: 5px;
}
@media (max-width: 744px) {
  .calendar__swiper-paggination-wrap {
    margin-top: 25px;
  }
}

.calendar__swiper {
  margin-bottom: 80px;
}
@media (max-width: 1080px) {
  .calendar__swiper {
    margin-bottom: 60px;
  }
}

.calendar__swiper-slide {
  width: 100%;
}
@media (max-width: 1080px) {
  .calendar__swiper-slide {
    width: auto;
  }
}
.calendar__swiper-slide:nth-child(n+2) {
  width: 0px;
}

.vc-arrows-container {
  display: none !important;
}

.calendar__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
@media (max-width: 1080px) {
  .calendar__header {
    grid-template-areas: "year year year month month month" "prev prev monthLabel monthLabel next next";
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-row-gap: 30px;
    margin-bottom: 40px;
  }
}
@media (max-width: 744px) {
  .calendar__header {
    grid-template-areas: "year year year" "month month month" "prev monthLabel next";
    grid-template-columns: 1fr 1fr 1fr;
    grid-row-gap: 20px;
    margin-bottom: 30px;
  }
}
.calendar__header .paggination__arrow {
  cursor: pointer;
}

.calendar__month {
  grid-area: monthLabel;
  text-align: center;
  font-weight: 600;
  font-size: 40px;
  line-height: 140%;
  letter-spacing: 0.06em;
  opacity: 0;
  display: flex;
  align-items: center;
}
@media (max-width: 1080px) {
  .calendar__month {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    line-height: 22px;
  }
}
@media (max-width: 744px) {
  .calendar__month {
    margin-top: 20px;
    font-size: 20px;
    line-height: 120%;
    letter-spacing: 0.04em;
  }
}

.calendar__month_active {
  opacity: 1;
  text-transform: capitalize;
}

.calendar__btn-text {
  color: $color_dark;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.06em;
  cursor: pointer;
}
@media (max-width: 1080px) {
  .calendar__btn-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 744px) {
  .calendar__btn-text {
    display: none;
  }
}

.calendar__next-month {
  grid-area: next;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.calendar__next-month .calendar__next-month-btn {
  width: 40px;
  height: 40px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.calendar__next-month .icon {
  width: 12px;
  height: 20px;
}
@media (max-width: 744px) {
  .calendar__next-month {
    margin-top: 20px;
  }
}

.calendar__prev-month {
  grid-area: prev;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.calendar__prev-month .calendar__prev-month-btn {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.calendar__prev-month .icon {
  width: 12px;
  height: 20px;
}
@media (max-width: 744px) {
  .calendar__prev-month {
    margin-top: 20px;
  }
}

.calendar__year-select {
  grid-area: year;
}
@media (max-width: 1080px) {
  .calendar__year-select {
    display: flex;
    justify-content: flex-end;
  }
}

.calendar__month-select {
  grid-area: month;
}

.calendar__download-text {
  text-align: center;
  margin-bottom: 20px;
}
.calendar__download-text h2 {
  color: var(--color-blue-dark);
  font-weight: 500;
  font-size: 40px;
  line-height: 48px;
}
@media (max-width: 1080px) {
  .calendar__download-text h2 {
    font-weight: 500;
    font-size: 26px;
    line-height: 31.2px;
  }
}
@media (max-width: 744px) {
  .calendar__download-text h2 {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
  }
}

.calendar__download-btn {
  display: flex;
  justify-content: center;
}

.calendar__top-text p a {
  color: var(--color-blue-dark);
}
.calendar__top-text p:first-child {
  margin-bottom: 25px;
}
@media (max-width: 1080px) {
  .calendar__top-text p:first-child {
    margin-bottom: 20px;
  }
}
@media (max-width: 744px) {
  .calendar__top-text p:first-child {
    margin-bottom: 15px;
  }
}
.calendar__top-text p:last-child {
  margin-bottom: 70px;
}
@media (max-width: 744px) {
  .calendar__top-text p:last-child {
    margin-bottom: 40px;
  }
}