.l-banner {
  height: 640px;
  position: relative;
  overflow: hidden;
  @include xm {
    height: 670px;
  }
  .l-banner__swiper {
    height: 100%;
    .swiper-wrapper {
      z-index: 10;
    }
    .swiper-slide {
      // position: relative;
      section {
        height: 100%;
      }
    }
  }

  .l-bg-video {
    .bg {
      z-index: 5;
      position: absolute;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
      width: 100%;
      height: 100%;
      object-fit: cover;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
    }
  }
  .l-banner__video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
  }

  .time-indicator {
    font-weight: 600;
    font-size: 18px;
    line-height: 140%;
    color: #f9f9f9;
    order: -1;
  }
  .video-progress-container {
    position: absolute;
    z-index: 20;
    bottom: 50px;
    margin: 0 auto;
    right: 0;
    left: 0;
    width: 200px;

    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 5px;
    @include xm {
      display: none;
    }
  }

  .video-progress {
    width: 100%;
    height: 2px;
    background-color: #858585;
    position: relative;
    border-radius: 10px;
  }

  .progress-bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: #f9f9f9;
    border-radius: 10px;
    transition: all 0.2s linear;
  }

  .marker {
    position: absolute;
    top: -5px;
    left: 0;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #ff1a1a;
    transform: translateX(-50%);
    transition: all 0.2s linear;
  }
}

//banner
.l-banner__content {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 100;
  width: 100%;
  height: 100%;
  @include xm {
    align-items: flex-end;
    padding-bottom: 40px;
  }
  .l-banner__title {
    color: #f9f9f9;
    font-weight: 600;
    font-size: 50px;
    line-height: 140%;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    max-width: 600px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    @include l {
      font-size: 40px;
    }
    @include xm {
      max-width: 100%;
      text-align: center;
      font-size: 32px;
    }
    @include xxs {
      font-size: 28px;
      line-height: 120%;
    }
  }
  .l-btn {
    margin-top: 30px;
    @include xm {
      margin: 30px auto 0 auto;
    }
  }
}

.orbit-logo {
  width: 50px;
  height: 50px;
}

.orbit-wrapper {
  width: 100%;
  height: 100%;
  top: -100%;
  position: relative;
}

.l-banner_navigation {
  position: absolute;
  right: -240px !important;
  top: 11% !important;
  left: unset !important;
  // background-color: #ff1a1a;
  top: 50%;
  z-index: 70;
  max-width: 500px;
  max-height: 500px;
  width: 100%;
  height: 100%;

  background-image: url(../../img/sprite.svg#css--orbit);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 120px;

  transition: transform 0.3s ease-in-out;

  @include l {
    right: -341px !important;
  }
  @include xm {
    max-width: 300px;
    max-height: 300px;
  }
  @include xm {
    right: -202px !important;
  }
  @include xxs {
    top: 5% !important;
    right: -229px !important;
  }
  .l-pagination__name {
    display: flex;
    align-items: center;
    cursor: pointer;
    z-index: 50;
    width: 24px;
    height: 24px;
    background-color: #f9f9f9;

    border-radius: 50%;
    position: absolute;
    top: 45%;
    right: 50%;
    transform-origin: center center;
    transform: translate(-50%, -50%) rotate(0deg);
    white-space: nowrap;
    transition: all 0.3s ease-in-out;
    @include xm {
      width: 16px;
      height: 16px;
    }
    div {
      position: absolute;
      right: 35px;
      color: #f9f9f9;
      font-weight: 400;
      font-size: 14px;
      line-height: 140%;
      letter-spacing: 0.06em;
      transition: all 0.3s ease-in-out;
    }
    &.active {
      font-weight: bold;
      font-size: 16px;
      background-color: #ff1a1a;
      div {
        font-weight: 500;
        font-size: 24px;
      }
    }
    &:hover {
      font-weight: bold;
      font-size: 16px;
      background-color: #ff1a1a;
      div {
        font-weight: 500;
        font-size: 24px;
      }
    }
  }

  &::after {
    content: "";
    border-radius: 50%;
    position: absolute;
    z-index: 0;
    top: 0;
    display: flex;
    width: 500px;
    height: 500px;
    border: 2px solid #f9f9f9;
    @include xm {
      width: 300px;
      height: 300px;
    }
  }
  span {
    display: none;
  }
}

.l-numbers__title span {
  color: #232323;
  font-weight: 600;
  font-size: 28px;
  line-height: 140%;
  letter-spacing: 0.06em;
  @include xxs {
    font-size: 18px;
  }
}

.l-numbers {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  .l-numbers__content {
    margin-top: 30px;
  }
}

.l-numbers__content {
  border-right: 1px solid #232323;
  border-left: 1px solid #232323;
  @include m-small {
    width: 100%;
  }
  ul {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    @include l-1100 {
      grid-template-columns: 1fr 1fr 1fr;
    }
    @include m-small {
      grid-template-columns: 1fr 1fr;
      gap: 15px;
      padding: 0 5px;
    }
  }
}

.l-num__list-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 200px;
  @include m-small {
    max-width: 100%;
  }
  .l-num__title {
    color: #ff1a1a;
    font-weight: 700;
    font-size: 38px;
    line-height: 140%;
    letter-spacing: 0.06em;
    @include xxs {
      font-size: 26px;
      line-height: 120%;
    }
  }
  .l-num__text {
    margin-top: 2px;
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #232323;
    @include xxs {
      font-size: 16px;
    }
  }
  .l-num__descr {
    margin-top: 5px;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #232323;
    text-align: center;
    @include xxs {
      font-size: 12px;
    }
  }
}

.l-invest__section {
  background-color: #1d1e33;
  text-align: center;
  overflow-x: hidden;
}

.l-invest {
  margin-top: 50px;
}

.l-invest__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  position: relative;
  @include xxs {
    padding-bottom: 25px;
  }
  .js-swiper-pag {
    display: none;
    position: absolute;
    right: 0;
    left: 0;
    @include xxs {
      display: flex;
      bottom: 0;
      .success-pagination {
        width: 200px !important;
      }
    }
  }
  .swiper-slide {
    max-width: 243px;
    width: 100% !important;
    @include xxs {
      max-width: 180px;
    }
  }
  .l-invest__list-item {
    max-width: 243px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    text-align: start;
    border: 1px solid #f9f9f9;
    padding: 20px 27px 20px 20px;
    height: 300px;
    text-decoration: none;
    @include xxs {
      height: 200px;
      padding: 15px 10px;
    }
  }
}

.l-invest__list-item {
  overflow: hidden;
  position: relative;
  &:hover {
    img {
      opacity: 1;
    }
    .arrow-next {
      bottom: 20px;
    }
    .title {
      padding-bottom: 25px;
    }
  }
  img {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    transition: all 0.4s;
  }
  .arrow-next {
    position: absolute;
    bottom: -40%;
    gap: 5px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #f9f9f9;
    transition: all 0.4s;
    svg {
      width: 14px;
      height: 10px;
    }
  }
  .title {
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #f9f9f9;
    transition: all 0.4s;
  }
  svg {
    width: 40px;
    height: 40px;
  }
}

