.l-circle {
  width: 600px;
  height: 600px;
  border-radius: 50%;

  position: absolute;
  right: 0;
  top: 38%;
  left: 0;
  margin: 0 auto;

  border: 2px solid #f9f9f9;
  @include xl-1300 {
    width: 300px;
    height: 300px;
    top: 50%;
    transform: translateY(-50%);
  }
  @include xm {
    width: 157px;
    height: 157px;
  }
}

.l-circle__list {
  position: relative;
  width: 600px;
  height: 100%;
  margin: 0 auto;
  margin-top: 50px;
  @include xl-1300 {
    display: grid;
    grid-template-columns: 80px 80px;
    align-items: center;
    justify-content: space-between;

    position: absolute;
    right: 0;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    height: 433px;
    width: 376px;
    margin: 0 auto;
  }
  @include xm {
    grid-template-columns: 40px 40px;
    height: 228px !important;
    width: 199px;
  }
  li {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 80px;
    height: 80px;
    background-color: #f9f9f9;
    border-radius: 50%;
    transition: all 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    @include xl-1300 {
      position: unset;
    }
    @include xm {
      width: 40px;
      height: 40px;
    }
    &.active {
      background-color: #ff1a1a;
      .l-circle__list--svg {
        stroke: white;
      }
    }
    &:nth-child(1) {
      bottom: 153px;
      left: -23px;
      @include xl-1300 {
        bottom: none;
        left: unset;
        grid-column-start: 1;
        grid-row-start: 2;

      }
      span {
        left: -168px;
        br {
          display: none;
        }
        @include m-880 {
          br {
            display: block;
          }
          left: -80px;
        }
        @include xxs {
          left: -62px;
        }
      }
    }
    &:nth-child(2) {
      top: -2px;
      left: 125px;
      @include xl-1300 {
        top: unset;
        grid-row-start: 1;
        grid-column-start: 2;
      }

      span {
        left: -241px;
        @include m-880 {
          left: unset;
          top: -30px;
        }
      }
    }
    &:nth-child(3) {
      top: -2px;
      left: 385px;
      @include xl-1300 {
        grid-column-start: 3;
        grid-row-start: 2;
      }
      span {
        right: -110px;
        @include xxs {
          right: -72px;
        }
      }
    }
    &:nth-child(4) {
      bottom: 153px;
      right: -23px;
      @include xl-1300 {
        grid-row-start: 3;
        grid-column-start: 2;
      }
      span {
        right: -283px;
        @include m-880 {
          right: unset;
          bottom: -30px;
        }
      }
    }

    span {
      position: absolute;
      white-space: nowrap;
    }
  }
}

.l-circle__list--svg {
  transition: all 0.4s ease;
  width: 40px;
  height: 40px;
  fill: transparent;
  stroke: #232323;
  stroke-width: 2px;
  @include xm {
    width: 24px;
    height: 24px;
  }
}

// success
.l-success__section {
  overflow-x: hidden;
  .l-title-main {
    text-align: center;
  }
  .l-btn {
    margin: 0 auto;
    margin-top: 40px;
  }
}

.js-swiper-pag {
  margin: 0 auto;
  display: flex;
  align-items: center;
  max-width: 460px;
  width: 100%;
  gap: 40px;
}

.success-button-next {
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24.3346 30L26.668 27.6667L19.0013 20L26.668 12.3333L24.3346 10L14.3346 20L24.3346 30Z' fill='%231D1E33'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
}

.l-success__swiper {
  position: relative;
  .swiper-wrapper {
    align-items: center;
  }
  .swiper-slide {
    position: relative;
    display: flex;
    justify-content: center;
    height: 350px;
    transition: all 0.4s;
    @include m-small {
      height: 200px;
    }
    &.swiper-slide-active {
      z-index: 111;
      .l-success__slide {
        max-width: 640px;
        @include l {
          max-width: 400px;
        }
        @include m-small {
          max-width: 260px;
        }
      }
    }
    &.swiper-slide-prev {
      transform: translateX(40%);
      height: 310px !important;
      // width: 310px !important;
      opacity: 0.6;
      @include m-small {
        height: 160px !important;
      }
    }
    &.swiper-slide-next {
      transform: translateX(-40%);
      // width: 310px !important;
      height: 310px !important;
      opacity: 0.6;
      @include m-small {
        height: 160px !important;
      }
    }

    img {
      position: absolute;
      right: 0;
      left: 0;
      top: 0;
      bottom: 0;

      width: 100%;
      height: 100%;
      z-index: -1;
      object-fit: cover;
    }
  }
}
.l-success__slide {
  position: relative;
  width: 100%;
  max-width: 500px;
  padding: 30px;
  transition: max-width .4s;
  @include xl {
    max-width: 350px;
  }
  @include l {
    max-width: 300px;
  }
  @include xm {
    max-width: 200px;
    padding: 15px;
  }
  .title {
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;

    letter-spacing: 0.06em;
    color: #ffffff;
    @include m-small {
      font-size: 16px;
    }
  }
  .descr {
    margin-top: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #ffffff;
    @include m-small {
      font-size: 12px;
      line-height: 120%;
    }
  }
}

.l-news__section {
  background-color: #1d1e33;
