    text-align: center;
  }
}

.l-news {
  .l-btn {
    margin: 0 auto;
    margin-top: 40px;
  }
}

.l-news__grid {
  display: grid;
  grid-template-columns: 1fr 310px 310px;
  grid-template-rows: 1fr 1fr;
  gap: 20px 20px;
  @include xl {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;

  }
  @include m-small {
    gap: 15px;
  }
  a {
    width: 100%;
    height: 100%;
    display: flex;
    &:nth-child(1) {
      grid-row-start: span 2;
      width: 100%;
      @include xl {
        grid-row-start: 1;
        grid-column-start: 2 span;
      }
      .l-item {
        max-width: 100%;
        height: 480px;
        @include m-small {
          height: 200px;
        }
      }
    }
    &:nth-child(4) {
      grid-column-start: 2;
      @include xl {
        grid-column-start: unset;
      }
    }
  }
  .l-item {
    width: 100%;
    max-width: 310px;
    position: relative;
    height: 230px;
    padding: 30px;
    z-index: 1;
    transition: all 0.4s;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include xl {
      max-width: 100%;
    }
    @include m-small {
      height: 140px;
      padding: 15px;
    }
    .descr {
      margin-top: 15px;
    }
    &:hover {
      img {
        opacity: 1;
      }
    }
    img {
      transition: all 0.4s;
      position: absolute;
      opacity: 0.7;
      left: 0;
      top: 0;
      height: 100%;
      z-index: -1;
      width: 100%;
    }
  }
}

