.l-table {
  border-collapse: collapse;
  width: 100%;
  text-align: center;
  max-width: 1297px;
  overflow-x: auto;
  .last {
    width: 100%;
  }
  td {
    border: 1px solid #1d1e33;
    padding: 14px 35px;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: 0.06em;
    color: #1d1e33;
    @include m-small {
      padding: 5px 20px;
    }
    
  }
}

.scroll {
  overflow-x: auto;
}

.territory-1 {
  @include xxm {
    flex-direction: column-reverse;
    gap: 20px;

  }
}

.accordion {
  list-style: none;
  box-shadow: 6px 6px 30px rgba(0,0,0,.2);
  .list.close {
    i {
      transform: rotate(0);
    }
  }
  .list {
    margin-bottom: 25px;

    &:last-child {
      margin-bottom: unset;
      .submenu {
        padding-bottom: 25px;
      }
    }
  }
  li {
      border-top: 1px solid #e2e8e9;
      &:first-child {
          border: none;
      }
  }
  > li.close .submenu {
      height: 0 !important;
      padding: 0 !important;
      opacity: 0;
  }
  .submenu {
      overflow: hidden;
      opacity: 1;
      height: auto;
      transition: padding 0.1s, height .3s, opacity .1s;
      padding: 25px 30px;
      padding-bottom: unset;
      @include m-small {
        padding: 15px 20px;
        
      }
      li a {
          padding: 15px;
          display: block;
          text-decoration: none;
          color: #374252;
          text-align: center;
          &:hover {
              background: #f3f7f8;
          }
      }
  }
  .subsubmenu {
    margin-top: 10px;
    margin-left: 15px;
    li {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: unset;
      }
    }
  }
  .submenu-toggle {
      background: #1D1E33;
      color: #F9F9F9;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 18px 30px;
      cursor: pointer;
      text-align: center;
      transition: background-color .3s;
      position: relative;
      @include m-small {
        text-align: start;
        padding: 15px 20px;
        gap: 5px;
      }
      i {
        min-width: 24px;
        min-height: 24px;
        max-width: 24px;
        min-height: 24px;
        background-size: 100%;
        background-repeat: no-repeat;
        transition: all .3s;
        transform: rotate(180deg);
        @include m-small {
          min-width: 16px;
          min-height: 16px;
          max-width: 16px;
          max-height: 16px;
        }
      }
      &:hover {
          background-color: #414d5e;
      }
  }
}

.l-accordion__content {
  .l-btn {
    margin: 0 auto;
    margin-top: 40px;
    text-align: center;
  }
}