.l-block--page-banner {
  width: 100%;
  max-height: 470px;
  height: 100%;
  img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  @include xm {
    height: 185px;
  }
}
.l-page_banner--wrap {
  display: flex;
  width: 100%;
  max-height: 470px;
  height: 100%;
  padding: 26px;
  border: 1px solid $color_dark;
  @include xm {
    padding: 15px;
  }
  
}