.l-block--team {
}

.l-team--grid {
  display: flex;
  flex-direction: column;
}

.l-team--row {
  display: flex;
  column-gap: 75px;
  justify-content: center;
  flex-wrap: wrap;
  &:first-child {
    column-gap: 170px;
    .l-team--item {
      margin-top: 0;
      position: relative;
      &:first-child {
        .l-team--img::after {
          content: '';
          width: 1px;
          height: 100%;
          position: absolute;
          right: -84px;
          top: 0;
          background: $color_gray;
        }
      }
    }
  }
  
}

.l-team--item {
  max-width: 345px;
  width: 100%;
  margin-top: 80px;
}

.l-team--img {
  position: relative;
  max-width: 345px;
  width: 100%;
  max-height: 440px;
  img {
    width: 100%;
    height: 100%;
  }
}

.l-team--name {
  margin-top: 20px;
  white-space: nowrap;
}

.l-team--desc {
  height: 80px;
  margin-top: 10px;
}

.l-team--hr {
  padding: 15px 0;
  hr {
    width: 100%;
    height: 1px;
    background: $color_gray;
  }
}

.l-team--contact {
  margin-bottom: 10px;
  span {
    margin-right: 8px;
  }
  a {
    transition: .2s;
    &:hover {
      color: $color_red;
    }
  }
}
