.l-coordinate--content {
  border-top: 1px solid $color_gray;
  display: flex;
  align-items: center;
  padding-top: 25px;
  @include m-950 {
    flex-direction: column-reverse;
    align-items: self-start;
  }
}

.l-coordinate--row {
  @include m-950 {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }
}

.l-coordinate--scrollbar {
  width: 100%;
  position: relative;
  display: none;
  .swiper-scrollbar {
    top: 0;
    background: #858585;
  }
  .swiper-scrollbar-drag {
    background: #FF1A1A;
    border-radius: 0;
  }
  @include m-950 {
    display: block;
  }
}

.l-coordinate--suggest {
  margin-right: 40px;
  a {
    display: flex;
    align-items: center;
    &:hover {
      svg {
        stroke: red;
      }
    }
  }
  svg {
    width: 32px;
    height: 32px;
    stroke: #858585;
    stroke-width: 2px;
    @include m-950 {
      width: 24px;
      height: 24px;
    }
  }
  @include m-950 {
    margin-right: 60px;
  }
}

.l-coordinate--drag {
  display: none;
  svg {
    width: 24px;
    height: 24px;
  }
  @include m-950 {
    display: flex;
    align-items: center;
    margin-left: 60px;
  }
}

.l-coordinate--years {
  width: 100%;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  overflow: hidden;

  .l-coordinate--slide {
    width: auto;
    display: flex;
    align-items: center;
    a {
      color: $color_gray;
      transition: .2s;
      &:hover {
        color: $color_dark;
      }
    }
  }
  .swiper-slide-active a {
    color: $color_dark;
  }
  @include m-950 {
    // overflow: auto;
  }
}

/* START TOOLTIP STYLES */
[tooltip] {
  position: relative; /* opinion 1 */
}

/* Applies to all tooltips */
[tooltip]::before,
[tooltip]::after {
  text-transform: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  user-select: none;
  pointer-events: none;
  position: absolute;
  display: none;
  opacity: 0;
}
[tooltip]::before {
  content: '';
  border: 5px solid transparent;
  z-index: 1001;
}
[tooltip]::after {
  content: attr(tooltip); 
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 12px 24px;
  box-shadow: 0 1em 2em -.5em rgba(0, 0, 0, 0.35);
  background: $color_dark;
  color: $color_white_gray;
  z-index: 1000;
}

/* Make the tooltips respond to hover */
[tooltip]:hover::before,
[tooltip]:hover::after {
  display: block;
}

/* FLOW: DOWN */
[tooltip][flow^="down"]::before {
  top: 100%;
  border-top-width: 0;
  border-bottom-color: $color_dark;;
}
[tooltip][flow^="down"]::after {
  top: calc(100% + 5px);
}
[tooltip][flow^="down"]::before,
[tooltip][flow^="down"]::after {
  left: 50%;
  transform: translate(-50%, .5em);
}
[tooltip][flow^="down"]::after {
  left: 0;
  transform: translate(-50%, .5em);
}

/* KEYFRAMES */
@keyframes tooltips-vert {
  to {
    opacity: .9;
    transform: translate(-50%, 0);
  }
}

@keyframes tooltips-horz {
  to {
    opacity: .9;
    transform: translate(0, -50%);
  }
}

/* FX All The Things */ 
[tooltip]:not([flow]):hover::before,
[tooltip]:not([flow]):hover::after,
[tooltip][flow^="up"]:hover::before,
[tooltip][flow^="up"]:hover::after,
[tooltip][flow^="down"]:hover::before,
[tooltip][flow^="down"]:hover::after {
  animation: tooltips-vert 300ms ease-out forwards;
}

[tooltip][flow^="left"]:hover::before,
[tooltip][flow^="left"]:hover::after,
[tooltip][flow^="right"]:hover::before,
[tooltip][flow^="right"]:hover::after {
  animation: tooltips-horz 300ms ease-out forwards;
}


