.l-page--header {
  padding-top: 40px;
  .l-wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

}
.l-breadcrumbs {
  padding-bottom: 20px;
  ul {
    display: flex;
    align-items: center;
    @include xm {
      overflow: auto;
    }
  }
  li {
    margin-right: 10px;
  }
  a,
  span {
    color: $color_gray;
    @include xm {
      white-space: nowrap;
    }
  }
  a:hover {
    color: $color_dark;
  }
  i.separator {
    display: block;
    width: 5px;
    height: 5px;
    background: #ff1a1a;
    transform: rotate(45deg);

  }
}

.l-mobile-page--sections-list {
  display: none;

  div.l-page--sections-list {
    @include xxs {
      padding-top: 20px;
    }
    ul {
      gap: 10px;
    }
    a {
      color:#232323;
      white-space: normal;
      @include xxs {
        font-size: 12px;
      }
    }
    i.separator {
      background-color: #232323;
    }
  }
  @include xm {
    display: block;
  }
}

.l-page--sections-list {
  padding: 20px 0;
  max-width: 870px;
  ul {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    @include xxs {
      gap: 10px;
    }
  }
  li {
    opacity: 0.9;
    &:hover {
      opacity: 1;
    }
  }
  i.separator {
    display: block;
    width: 1px;
    height: 15px;
    background: $color_dark;
    @include xxs {
      display: none;
    }
  }
  a {
    color: $color_dark;
    text-decoration-line: underline;
    white-space: nowrap;
  }
}

@media screen and (max-width: 425px) {
  .l-breadcrumbs {
    padding-bottom: 20px;
    ul {
      overflow: auto;
    }
    a,
    span {
      white-space: nowrap;
    }
  }
  .l-page--sections-list {
    padding: 25px 0 10px 0;
  }
}
