.l-block--projects-cards {
}
.l-projects_cards--container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.l-projects_cards--item {
  position: relative;
  max-width: 420px;
  width: 100%;
  height: 240px;
  
  border: 1px solid $color_dark;
  transition: .2s;
  a {
    display: block;
    width: 100%;
    height: 100%;
    padding: 30px;
  }
  &:hover {
    background: $color_dark;
    color: $color_white;
    .l-projects_cards--item_icon {
      opacity: 1;
    }
  }
}
.l-projects_cards--item_name {
  margin-bottom: 15px;
}

.l-projects_cards--item_desc {
}

.l-projects_cards--item_icon {
  position: absolute;
  right: 30px;
  top: 30px;
  opacity: 0;
  transition: .8s;
}