.l-header {
  background-color: #1d1e33;
  padding: 22px 0 23px 0;
}
.l-header__content {
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  .glass {
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    transition: opacity .2s;
    display: none;
  }

}

.active-header {
  .lang {
    display: none;
  }
  .glass {
    display: block;
    visibility: visible;
    pointer-events: all;
    z-index: 100;
    opacity: 1;
  }
}

.l-header__mobile {
  display: none;
  @include m-640 {
    display: block;
    .l-header__list ul,
    .l-logo {
      display: flex;
    }
  }
}

.l-logo {
  position: relative;
  z-index: 100;
  @include m-640 {
    display: none;
  }
  .l-logo__link {
    width: 250px;
    height: 20px;
    display: block;
    @include xxs-big {
      max-width: 188px;
    }
  }
}
.l-logo__link svg {
  width: 100%;
  height: 100%;
}

.l-header_nav-list {
  display: flex;
  align-items: center;
  gap: 25px;
  @include xxl {
    display: none;
  }
}

.l-header_nav-list-link {
  padding: 5px 0;
  display: block;
  white-space: nowrap;

  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.06em;
  color: #f9f9f9;

  transition: all 0.4s ease;
  &:hover {
    color: #ff1a1a;
  }
  &.active-link {
    color: #ff1a1a;
  }
}

.l-header__list {
  padding-right: 57px;
  position: relative;
  @include m-small {
    padding-right: unset;
  }
  ul {
    gap: 25px;
    @include m-640 {
      display: none;
    }
  }
  svg {
    width: 32px;
    height: 32px;
    display: block;
  }
}

.js-search-icon {
  stroke: #f9f9f9;
  fill: transparent;
  cursor: pointer;
}

.search-bar__block {
  position: absolute;
  width: 100vw;
  height: 0;
  background-color: #1d1e33;
  left: 0;
  top: 77px;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  z-index: 88;
  .l-wrapper {
    height: 100%;
  }
  &.active {
    height: 50px;
    .search-bar {
      display: flex;
    }
  }
  .search-bar {
    width: 100%;
    padding: 6px 0;
    display: flex;
    align-items: center;
    gap: 20px;
    display: none;
  }
  .js-search-input {
    @include no-btn;
    width: 100%;
    height: 100%;
    color: #f9f9f9;
    font-size: 24px;
    &:focus-within {
      outline: none;
      filter: drop-shadow(0px 4px 4px rgba(222, 222, 222, 0.45));
    }
  }
}

.l-burger__btn {
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 4px;
  z-index: 100;

  height: 23px;
  width: 32px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.5s ease-in-out;
  @include m-640 {
    top: -11px;
  }
  &.active {
    right: -7px;
    .line {
      width: 97%;
    }
    .line1 {
      transform: rotate(42deg);
    }
    .line2 {
      transform: scaleY(0);
    }
    .line3 {
      transform: rotate(-43deg);
    }
  }
  .line {
    display: block;
    height: 3px;
    width: 100%;
    background: #f9f9f9;
  }
  .line1 {
    transform-origin: 0% 0%;
    transition: transform 0.4s ease-in-out;
  }
  .line2 {
    transition: transform 0.2s ease-in-out;
  }
  .line3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }
}

.l-header__body {
  position: absolute;
  width: 101vw;
  height: auto;
  left: -5px;
  top: 0;
  padding:68px 0 60px 0;
  background-color: #f9f9f9;
  z-index: 110;
  margin-top: 77px;
  transform: translateY(-300%);
  transition: all 0.5s ease;
  -webkit-box-shadow: 1px 10px 8px 0px rgba(34, 60, 80, 0.2);
-moz-box-shadow: 1px 10px 8px 0px rgba(34, 60, 80, 0.2);
box-shadow: 1px 10px 8px 0px rgba(34, 60, 80, 0.2);
  @include m-small {
    padding: 30px 0;
    left: 0;
  }
  &.active {
    transform: translateY(0);
  }
}

.l-header__grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 400px));
  gap: 50px 100px;
  margin-top: 50px;
  @include xl {
    gap: 40px 30px;
  }
  @include l-1100 {
    grid-template-columns: 1fr 1fr;
  }
  @include m-small {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  li {
    .last-child {
      margin-top: 42px;
      .big-word {
        a {
          font-weight: 500;
          text-transform: uppercase;
          font-size: 20px;
          @include m-small {
            font-size: 16px;
          }
        }
      }
      li {
        margin-bottom: 20px;
        @include m-small {
          margin-bottom: 10px;
        }
        a {
          font-weight: 400;
          font-size: 16px;
          line-height: 140%;
          letter-spacing: 0.06em;
          color: #232323;
          transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
          @include m-small {
            font-size: 14px;
          }
          @include xxs {
            font-size: 12px;
          }
          &:hover {
            color: #ff1a1a;
          }
        }
      }
      @include m-small {
        margin-top: 20px;
      }
    }
  }
}

.l-header__grid-list {
  li {
    margin-bottom: 20px;
    @include m-small {
      margin-bottom: 10px;
    }
    a {
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      letter-spacing: 0.06em;
      color: #232323;
      transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
      @include m-small {
        font-size: 14px;
      }
      @include xxs {
        font-size: 12px;
      }
      &:hover {
        color: #ff1a1a;
      }
    }
    &:first-child {
      margin-bottom: 30px;
      @include m-small {
        margin-bottom: 20px;
      }
      a {
        font-weight: 500;
        text-transform: uppercase;
        font-size: 20px;
        @include m-small {
          font-size: 16px;
        }
      }
    }
    &:last-child {
      margin-bottom: unset;
    }
  }
}

.l-header__body-search {
  max-width: 417px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;

  input {
    @include no-btn;

    width: 100%;
    padding: 11px 0;
    border-bottom: 1px solid #232323;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.04em;
    color: #232323;
    &:focus-visible {
      outline: unset;
    }
  }

  svg {
    position: absolute;
    right: 0;
    width: 24px;
    height: 24px;
    stroke: #1d1e33;
    fill: transparent;
  }
}
