.l-block--gallery {
  margin-top: 40px;
}
.l-gallery--wrapper {
  display: flex;
  gap: 20px;
  @include xm {
    flex-direction: column;
  }
}
.l-gallery--item {
  max-width: 420px;
  width: 100%;
  max-height: 280px;
  padding: 25px;
  border: 1px solid $color_dark;
  @include xm {
    max-width: 420px;
    height: 185px;
    padding: 15px;
  }
  img {
    width: 100%;
    height: 100%;
  }
}