*,
*::before,
*::after {
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.--dir-col {
  flex-direction: column;
}

.unscroll {
  overflow: hidden;
}

.overflow-x {
  overflow-x: hidden;
}

html {
  // font-size: 16px;
  scroll-behavior: smooth;
}

html,
body {
  min-width: 320px;
  // position: relative;
}

body {
  font-family: "Gilroy", sans-serif;
  overflow-x: hidden;
  text-rendering: optimizeSpeed;

  background: url('/img/bg.png');
}

main {
  padding-bottom: 100px;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

hr {
  margin: 0;
  border: none;
}
.hr-0 {
  border-bottom: 1px solid #858585;
  margin-top: 26px;
}


a {
  color: inherit;
  text-decoration: none;
}

.l-wrapper {
  @include container("1440px", "70px");
  @include xl {
		padding: 0 40px;
	}
  @include m-small {
    padding: 0 30px;
  }
  @include xxs {
    padding: 0 20px;
  }

  @include xxxs {
    padding: 0 15px;
  }
  @include kura {
    padding: 0 10px;
  }
}

.l-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 13px 35px;
  text-decoration: unset;
  border: 1px solid transparent;

  cursor: pointer;

  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
  letter-spacing: 0.06em;
  // max-width: 268px;
  width: fit-content;
  @include m-small {
    font-size: 12px;
    line-height: 120%;
  }
  &__red {
    background-color: $color_red;
    color: $color_white_gray;
    border: 1px solid $color_red;
    &:hover {
      background-color: transparent;
      color: $color_red;
    }
  }
  &__white {
    background-color: transparent;
    color: $color_red;
    border: 1px solid $color_red;
    &:hover {
      background: #ff1a1a;
      box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
      color: white;
    }
  }
  &__white-gray {
    background-color: transparent;
    color: #F9F9F9;
    border: 1px solid #F9F9F9;
    &:hover {
      background: #ff1a1a;
      border: 1px solid #ff1a1a;
      box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
      color: white;
    }
  }
  @include transit(0.3);
}

.l-section {
  padding: 80px 0;
  @include xm {
    padding: 60px 0;
  }
  @include xxs {
    padding: 50px 0;
  }
}

.relative {
  position: relative;
}

main.l-mountain {
  padding-bottom: unset;
  position: relative;
  background: url(../../img/bg-mountain.png);
}

.l-title-main {
  font-weight: 600;
  font-size: 40px;
  line-height: 140%;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  @include xm {
    font-size: 28px;
  }
  @include xxs {
    font-size: 24px;
    line-height: 120%;
  }
  &.white {
    color: #f9f9f9;
  }
  &.dark {
    color: #232323;
  }
}
.hidden {
  display: none;
}

.mt-50 {
  margin-top: 50px;
  @include m-small {
    margin-top: 35px;
  }
}

.mt-80 {
  margin-top: 80px;
  @include xm {
    margin-top: 60px;
  }
  @include xxxs {
    margin-top: 50px;
  }
}

.mt-40 {
  margin-top: 40px;
  @include xm {
    margin-top: 30px;
  }
}
.mt-30 {
  margin-top: 30px;
}

.mt-25 {
  margin-top: 25px;
  @include xm {
    margin-top: 20px;
  }
}

.mt-28 {
  margin-top: 28px;
}

.mt-28-reverse {
  margin-top: -28px;
}

.mt-20 {
  margin-top: 20px;
}

.mt-15 {
  margin-top: 15px;
}

.mt-10 {
  margin-top: 10px;
}

